import React from "react";
import { Link, useNavigate } from "react-router-dom";
import { Navbar, Container, Nav, NavDropdown } from "react-bootstrap";
import { signOut } from "firebase/auth";
import { auth } from "../firebase";
import styles from "./MainNavbar.module.css";

//Navbar Part
function MainNavbar({isLogged, setIsLogged}) {
    //  
    const navigate = useNavigate();
    const signout = async () => {
        localStorage.clear()
        await signOut(auth);
        setIsLogged(false)
        navigate(0);
    }
    return (
        <>
            <Navbar collapseOnSelect expand="lg" bg="light" className={styles.NavbarDiv}>
                {/* variant="dark" */}
                <Container>
                <Navbar.Brand as={Link} to="/" className={styles.brand}></Navbar.Brand>
                <Navbar.Toggle aria-controls="responsive-navbar-nav" />
                <Navbar.Collapse id="responsive-navbar-nav">  
                <Nav className="me-auto">
                    <Nav.Link as={Link} to="/man" title=""></Nav.Link>
                    <Nav.Link as={Link} to="/woman" title=""></Nav.Link>
                    <Nav.Link as={Link} to="/kids" title=""></Nav.Link>

                    {/*   :     . */}
                    {/* <NavDropdown title="" id="collasible-nav-dropdown">
                    <NavDropdown.Item href="#action/3.1">//</NavDropdown.Item>
                    <NavDropdown.Item href="#action/3.2"></NavDropdown.Item>
                    <NavDropdown.Item href="#action/3.3">//</NavDropdown.Item>
                    <NavDropdown.Item href="#action/3.4">/</NavDropdown.Item>
                    </NavDropdown>
                    <NavDropdown title="" id="collasible-nav-dropdown">
                    <NavDropdown.Item href="#action/4.1">//</NavDropdown.Item>
                    <NavDropdown.Item href="#action/4.2"></NavDropdown.Item>
                    <NavDropdown.Item href="#action/4.3">//</NavDropdown.Item>
                    <NavDropdown.Item href="#action/4.4">/</NavDropdown.Item>
                    </NavDropdown> */}
                    
                </Nav>
                <Nav>
                    <Nav.Link as={Link} to="/watched" title="  ">  </Nav.Link>
                    <Nav.Link as={Link} to="/cart" title=""></Nav.Link>
                    {isLogged && <Nav.Link as={Link} to="/mypage" title=""></Nav.Link>}
                    {/*  */}
                    {!isLogged && <Nav.Link as={Link} to="/auth" title=""></Nav.Link>}
                    {/*  */}
                    {isLogged && <Nav.Link onClick={signout} title=""></Nav.Link>}
                </Nav>
                </Navbar.Collapse>
                </Container>
            </Navbar>
        </>
    )
}

export default MainNavbar;